import NcAvatar from '@nextcloud/vue/components/NcAvatar';
import NcButton from '@nextcloud/vue/components/NcButton';
import NcEmptyContent from '@nextcloud/vue/components/NcEmptyContent';
import NcPopover from '@nextcloud/vue/components/NcPopover';
import NcTextField from '@nextcloud/vue/components/NcTextField';
import IconAlertCircleOutline from 'vue-material-design-icons/AlertCircleOutline.vue';
import IconMagnify from 'vue-material-design-icons/Magnify.vue';
export default {
  name: 'SearchableList',
  components: {
    IconMagnify,
    IconAlertCircleOutline,
    NcAvatar,
    NcButton,
    NcEmptyContent,
    NcPopover,
    NcTextField
  },
  props: {
    labelText: {
      type: String,
      default: 'this is a label'
    },
    searchList: {
      type: Array,
      required: true
    },
    emptyContentText: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      opened: false,
      error: false,
      searchTerm: ''
    };
  },
  computed: {
    filteredList() {
      return this.searchList.filter(element => {
        if (!this.searchTerm.toLowerCase().length) {
          return true;
        }
        return ['displayName'].some(prop => element[prop].toLowerCase().includes(this.searchTerm.toLowerCase()));
      });
    }
  },
  methods: {
    clearSearch() {
      this.searchTerm = '';
    },
    itemSelected(element) {
      this.$emit('item-selected', element);
      this.clearSearch();
      this.opened = false;
    },
    searchTermChanged(term) {
      this.$emit('search-term-change', term);
    }
  }
};